import casesSeed from "@/data/mock_cases.json";
import { CaseSchema, DecisionSchema, type Case, type Decision } from "@/lib/schema";

type Store = {
  cases: Map<string, Case>;
};

function getStore(): Store {
  const g = globalThis as any;

  if (!g.__ATLAS_STORE__) {
    const parsed = (casesSeed as unknown[]).map((c) => CaseSchema.parse(c));
    const map = new Map<string, Case>();
    for (const c of parsed) map.set(c.id, c);

    g.__ATLAS_STORE__ = { cases: map } satisfies Store;
  }

  return g.__ATLAS_STORE__ as Store;
}

export function getAllCases(): Case[] {
  const store = getStore();
  return Array.from(store.cases.values()).sort((a, b) => b.created_at.localeCompare(a.created_at));
}

export function getCaseById(id: string): Case | null {
  const store = getStore();
  return store.cases.get(id) ?? null;
}

function nowIso(): string {
  return new Date().toISOString();
}

export function decideCase(raw: unknown): Case {
  const decision: Decision = DecisionSchema.parse(raw);
  const store = getStore();

  const c = store.cases.get(decision.case_id);
  if (!c) throw new Error("Case not found");

  let newStatus: Case["status"] = c.status;
  if (decision.decision === "APPROVE") newStatus = "APPROVED";
  if (decision.decision === "REJECT") newStatus = "REJECTED";
  if (decision.decision === "REQUEST_INFO") newStatus = "NEEDS_MORE_INFO";

  const detail =
    decision.decision === "APPROVE"
      ? `APPROVED. ${decision.comment ?? ""}`.trim()
      : decision.decision === "REJECT"
      ? `REJECTED. ${decision.comment ?? ""}`.trim()
      : `REQUESTED INFO. ${decision.comment ?? ""}`.trim();

  const updated: Case = {
    ...c,
    status: newStatus,
    history: [
      ...c.history,
      {
        ts: nowIso(),
        actor: "sarah",
        event: "decided",
        detail,
      },
    ],
  };

  store.cases.set(updated.id, updated);
  return updated;
}
